<template>
    <div class="mui-propup">
        <div class="layout">
            <h2>提示</h2>
            <p>请输入提款密码</p>
            <input v-model="pass_text" type="password" maxlength="22" placeholder="请输入提现密码">
            <div class="btn">
                <span @click="cancel">取消</span>
                <span @click="confirm">确定</span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                pass_text: '',
            }
        },
        methods: {
            confirm(){
                this.$emit('confirm', this.pass_text)
            },
            cancel(){
                this.$emit('cancel', false)
            }
        }
    }
</script>

<style lang="stylus">
    .mui-propup
        position fixed
        left 0
        right 0
        bottom 0
        top 0
        background rgba(#000, .6)
        .layout
            background #fff
            border-radius .1rem
            position absolute
            top 30%
            left 10%
            right 10%
            padding .3rem 6%
            text-align center
            input
                border solid 1px #eee
                height .72rem
                margin-top .3rem
                width 100%
                padding 0 .25rem
                text-align center
            h2
                font-size .4rem
            p
                font-size .3rem
                margin-top .2rem
            .btn
                display flex
                justify-content space-between
                margin-top .3rem
                padding 0 .6rem
                span
                    display block
                    font-size .34rem
                    color #ff3e44

</style>