<template>
    <div class="container back-one">
        <div class="withdrawal">
            <div class="money">
                <h3>提现金额</h3>
                <div class="input">
                    <span class="sp1">￥</span>
                    <b-input @maxMessage="maxMessage" :auto="false" v-model="config.money" :maxNumber="appConfig.qkMaxMoney" placeholder="请输入提现金额"></b-input>
                    <span class="sp2">元</span>
                </div>
                <div class="info">
                    <div style="width: 60%;float: left;">
                        余额￥<span>{{myInfo.balance >= 0 ? myInfo.balance : myInfo.result==108 ? '未登录' : '获取中'}}</span>元，<b @click="allMoney">全部提现</b>
                    </div>
                    <div style="width: 40%;float:right;text-align: right;">
                        <p>
                          提款打码量:{{needDml || 0}}
                        </p>
                    </div>
                </div>
            </div>
            <div class="default-bank">
                <div class="item" v-if="list.length > 0" @click="show=true">
                    <div class="pict">
                        <img :src="require(`@/static/img/bank/${bankPicture(list[index].bankName)}`)" alt="">
                    </div>
                    <div class="text">
                        <h3>{{list[index].bankName}}（尾号{{list[index].bankAccount | lotteryNumber(4)}}  {{list[index].subBankName}}）</h3>
                        <p>极速到账  *单笔金额:10~10000元</p>
                    </div>
                </div>
                <router-link v-else class="null" to="/bankCard">前往添加银行卡></router-link>
            </div>
            <div class="goto-bank">
                <router-link class="fr" to="/bankCard">前往管理银行卡></router-link>
            </div>
            <div class="public-btn mt40">
                <button class="button" @click="inputPass">立即提现</button>
                <button class="button b1" @click="gotoTixian">提现记录</button>
            </div>
        </div>
        <van-popup
        v-model="show"
        position="bottom"
        :style="{ height: '40%' }"
        >
        <van-picker
            title="请选择银行卡"
            show-toolbar
            :columns="columns"
            @confirm="onConfirm"
            @cancel="show=false"
            visible-item-count="4"
            />
        </van-popup>
        <inputComponent v-if="show1" @confirm="confirm" @cancel="show1=false"></inputComponent>
    </div>
</template>

<script>
import { GAME_GET_USER_BASE_INFO } from "@/tool/common/constants.js";
import { mapState } from "vuex";
import inputComponent from "./inputComponent.vue";
export default {
    components: {
        inputComponent
    },
    data(){
        return {
            show: false,
            show1: false,
            needDml:"",
            list: [],
            index: 0,
            config: {
                id: '',
                money: '',
                drawPassword: ''
            },
            appConfig: {},

        }
    },
    created(){
        var user = this.$user()
        this.$store.commit('User/SET_USER', user)
        this.getInfo()
        this.getBankCard()
        this.getApp()
        this.getUserSession()
        if(this.env.app){
            var json = '{"title":"提现","rightTitle":{"name" :"","url":"/withdrawalPass"},"showNai":"y","back":"h5"}'
            this.callHandle(json)
        }
    },
    computed: {
        ...mapState('Game', {
            myInfo: state => state.getUserBaseInfo
        }),
        columns(){
            var arr = []
            this.list.forEach((item, index)=>{
                if(item.default){
                    this.index = index
                    this.config.id = item.id
                }
                arr.push(item.bankName + '-' + this.Number(item.bankAccount))
            })
            return arr
        },
    },
    methods: {
        gotoTixian(){
            this.$router.push('/withdrawalRecord');
        },
        confirm(text){
            this.show1=false;
            this.config.drawPassword = this.$md5(text)
            this.drawal()
        },
        getUserSession(){
            const _this = this
            this.$api.user.getUserSession(this.baseUser)
            .then(res=>{
                console.log(res);
                _this.needDml = res.drawingMoney
                if(this.appConfig.needWithdrawPasswd){
                    if(!res.hasSetDrawPassword){
                        _this.mui.confirm('取款密码尚未设置，是否设置密码？','温馨提示',['取消','确定'], function(res){
                            if(res.index){
                                _this.$router.push({path: '/withdrawalPass', query: _this.baseUser})
                            }
                        }, 'div')
                    }
                }
            })
        },
        getApp(){
            this.$api.user.getAppConfig(this.baseUser)
            .then(res=>{
                // console.log(res);
                this.appConfig = res
                this.getUserSession()
            })
        },
        maxMessage(money){
            this.$toast(`提现金额不能大于${money}元`)
        },
        inputPass(){
            const _this = this

            if(this.config.money < this.appConfig.qkMinMoney){
                this.$toast(`提现金额不能小于${this.appConfig.qkMinMoney}元`)
                return;
            }
            if(this.list.length == 0){
                this.$toast('请先添加一张银行卡')
                return;
            }
            if(this.appConfig.needWithdrawPasswd){
                _this.show1 = true
            }else{
                _this.drawal()
            }
        },
        drawal(){
            var data = {
                ...this.baseUser,
                ...this.config
            }
            this.$api.user.submitWithdraw(data)
            .then(res=>{
                if(res.result === 1){
                    this.gotoTixian();
                    this.$toast('您已成功提交申请，请查看提现记录')
                }else{
                    this.$toast(res.description)
                }
            })
        },
        Number(data){
            var str = data
            var s1 = str.substring(0, 6)
            var s2 = str.substring(str.length - 3, str.length)
            return s1 + '****' + s2
        },
        onConfirm(e, index){
            this.index = index
            this.config.id = this.list[index].id
            this.show = false
        },
        getInfo(){
            this.$store.dispatch(`Game/${GAME_GET_USER_BASE_INFO}`, this.baseUser)
        },
        allMoney(){
            var money
            if(this.myInfo.balance > this.appConfig.qkMaxMoney){
                money = this.appConfig.qkMaxMoney;
                this.maxMessage(money);
            }else{
                money = this.myInfo.balance;
            }
            this.config.money = parseInt(money);
        },
        getBankCard(){
            this.$api.user.getUserBankCardList(this.baseUser)
            .then(res=>{
                console.log(res);
                this.list = res.userBankCardList;
            })
        }
    },
}
</script>

